<template>
  <div>
    <v-toolbar dense height="60px" color="error">
      <v-toolbar-title>
        <router-link to="/">
          <v-btn icon><v-icon size="35" color="white">mdi-home</v-icon></v-btn>
        </router-link>
      </v-toolbar-title>
    </v-toolbar>
  <v-container>
    <v-layout align-center justify-center>
      <v-responsive class="mx-auto" max-width="400px">
        <v-card class="my-5 border rounded-3 elevation-4">
          <form @submit.prevent="Login">
            <v-card-title class="rounded-top">Admin Login</v-card-title>
            <v-card-text>
              <v-text-field type="email" placeholder="Email Address" :rules="emailRules" v-model="form1.email"
                            prepend-inner-icon="mdi-email" required>
              </v-text-field>
              <v-text-field v-model="form1.user_role" class="d-none" name="user_role"></v-text-field>
              <v-text-field type="password" placeholder="Password" :rules="passwordRules"  v-model="form1.password"
                            prepend-inner-icon="mdi-lock" required>
              </v-text-field>
              <div v-if="error" class="alert alert-danger">{{error}}</div>
<!--              <div class="text-md-end">-->
<!--                <router-link to="/forgetpassword" class="text-muted text-decoration-none">Forget Password</router-link>-->
<!--              </div>-->
              <div class="mt-3 align-center">
                <v-btn type="submit" large block color="error">Login</v-btn>
              </div>
            </v-card-text>
          </form>
        </v-card>
      </v-responsive>
    </v-layout>
  </v-container>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "AdminLogin",
  metaInfo: {title: 'Bizlx Admin Login'},
  data() {
    return {
      form1: {
        email: '',
        password: '',
        user_role: '4'
      },
      token: '',
      user: '',
      snackbar: false,
      error: '',
      timeout: '',
      emailRules: [
        v => !!v || 'E-mail is required.',
        v => /.+@.+/.test(v) || 'E-mail must be valid.',
      ],
      passwordRules: [
        v => !!v || 'Password is required.',
        v => /[A-Z]/.test(v) || 'Use upper case.',
        v => /[a-z]/.test(v) || 'Use lower case.',
        v => /[0-9]/.test(v) || 'Use number.',
        v => /[#?!@$%^&*-]/.test(v) || 'Use special character.',
        v => v.length >= 8 || 'Password must be greater than 8 characters.',
        v => v.length <= 15 || 'Password must be less than 15 characters.',
      ],
    }
  },
  created() {
    this.isAuthenticated();
    this.user_role();
    if( this.isAuthenticated() == true){
      if(this.user_role() == 4){
        this.$router.push({name: 'dashboard'})
      }
      if(this.user_role() == 1){
        this.$router.push({name: 'businesslogin'})
      }
      if(this.user_role() == 2){
        this.$router.push({name: 'resellerlogin'})
      }
      if(this.user_role() == 3){
        this.$router.push({name: 'admin'})
      }
      if(this.user_role() == 0){
        this.$router.push({name: 'userlogin'})
      }
    }
    else {
      this.$router.push({name: 'adminlogin'});
    }
  },
  methods: {
    async Login() {
      // Make API call to authenticate the user and obtain token and user data
      await axios.post('admin/login', this.form1)
          .then((resp) => {
            this.token = resp.data.token;
            this.user = resp.data.user;
            const token = this.token;
            const userData = this.user;
            this.$store.dispatch('login', { token, userData });
            this.$router.push('dashboard');
            location.reload();
          })
          .catch((err)=>{
            this.error = err.response.data.message;
          })
    },
    isAuthenticated() {
      return this.$store.state.isAuthenticated;
    },
    user_role() {
      return this.$store.state.userData.user_role;
    },
  }
}
</script>

<style scoped>
.bgred {background-color: #fd0606;}
</style>